import React, { useState } from 'react'
const App: React.FC = () => {

  const [url, setUrl] = useState('')
  const [videoID, setVideoID] = useState('')

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault()
    let id = url.split('v=')[1]
    const ampersandPosition = id.indexOf('&')
    if(ampersandPosition !== -1) {
      id = id.substring(0, ampersandPosition)
    }
    setVideoID(id)
  }

  return (
    <div className="main">
      <div className="container">
        <div className="title">
          <h1>Watch</h1>
          <h1 className="alone">Alone</h1>
        </div>
        <iframe title="ytFrame" src={`https://www.youtube.com/embed/${videoID}`}></iframe>
        {!url ? <p>Please enter URL:</p>: <p>URL:</p>}
        <a href={url}>{url}</a>
        <form onSubmit={handleSubmit}>
          <input type="text" value={url} onChange={event => setUrl(event.target.value)} placeholder="Youtube URL here" />
          <input type="submit" value="Search"></input>
        </form>
      </div>
    </div>
  );
}

export default App;
